@import "../../styles/variables";

.obras {
  padding: 0 0 40px;

  &__subtitle {
    color: white;
    font-family: $font-family-subtitle, sans-serif;
    text-align: center;
    font-size: 20px;
    margin: 0 auto 20px;
  }

  &__box {
    display: flex;
    padding: 0 20px;
  }
}

.slide {
  font-family: $font-family-title;
  font-weight: 700;
  line-height: 2.6em;
  height: 50vh;
  border: 1px solid white;
  border-radius: 20px;
  margin: 10px;
  cursor: pointer;
  text-align: center;
  color: #fff;
  text-shadow: 0 0 10px $colorTertiary;
  flex: 1;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
  transition: all 500ms ease-in-out;

  &__link {
    color: white;
    font-family: roboto, sans-serif;
    font-weight: 400;
    opacity: 0;
    transition: opacity 0.3s ease-in 0.4s;
    background-color: $colorSecondary;
    padding: 5px 7px;
    border-radius: 25px;
    pointer-events: none;
    text-shadow: none;
  }

  &__link:hover {
    background-color: rgba(0, 0, 0, 0.468);
    transition: opacity 0.3s ease-in 0.4s;
  }
}

.slide:hover {
  border: 1px solid $colorTertiary;
  box-shadow: 0 0 2px $colorTertiary;
}

.slide h3 {
  position: absolute;
  font-size: 20px;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  margin: 0;
  opacity: 0;
  background-color: $colorSecondary;
  padding: 3px 10px;
  border-radius: 25px;
  line-height: 1.3em;
  box-shadow: 0 0 5px $colorSecondary, 0 0 5px #000;
  font-weight: 400;
  text-shadow: none;
}

.slide.active {
  flex: 9;
  cursor: auto;
}

.slide.active h3 {
  opacity: 1;
  transition: opacity 0.3s ease-in 0.4s;
}

.slide.active .slide__link {
  opacity: 1;
  pointer-events: all;
  transition: opacity 1.2s ease-in 0.8s;
}


@media (max-width: 883px) {
  .obras {
    &__box {
      display: block;
      padding: 0;
    }
    &__subtitle {
      display: none;
    }

    &__title {
      margin-bottom: 40px;
    }
  }

  .slide {
    height: 50vh;
    border: 1px solid white;
    margin: 0 10px 45px;
    border-radius: 7px;
    cursor:default;

    &__link {
      opacity: 1;
      cursor: pointer;
    }
  }

  .slide:hover {
    border: 1px solid white;
    box-shadow: none;
  }
  
  .slide h3 {
    opacity: 1;
  }
  
}

@media (max-width: 500px) {
  .obras {
    display: block;

    &__box {
      overflow: hidden;
      display: block;
    }
  }
  .slide {
    width: 100%;
    border-radius: 10px;
    position: relative;
    top: 0;
    left: 0;
    display: block;
    margin: 10px 0 45px;
  }

 

  .slide h3 {
    font-size: 16px;
    padding: 2px 10px;
    line-height: 1.1em;
  }
}